//NAVIGATION
.page-header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-top: $gutter-sm;
  padding-bottom: $gutter-sm;
  @include layout-gutter-x; }

.navigation-mobile {
  display: none;
  @include media(sm) {
    display: block; } }
.navigation-primary {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  @include media(sm) {
    display: none; }
  &__item {
    margin-right: $gutter-md; }
  &__cta {
    margin: 0 0 0 auto; } }

.logo__image {
  height: 40px;
  width: auto; }

// MAIN
.section-featured {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  &.theme-dark {
    background-color: $color-primary; }
  &__content {
    @include layout-gutter-y;
    @include layout-gutter-x; }
  .featured-image {
    &__image {
      object-fit: cover; } } }



.section-featured--reversed {
  margin-bottom: $gutter-sm;
  .section-featured__content {
    grid-row: 1; } }

.text-block {
  &__content {
    padding-left: $gutter-md; } }

.section-banner {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-top: 10rem;
  padding-bottom: 10rem;
  @include layout-gutter-x;
  &__image {
    overflow: hidden;
    z-index: 5;
    grid-row: 1/2;
    grid-column: 1/3; }
  &__content {
    padding: $gutter-md;
    z-index: 10;
    grid-row: 1/2;
    grid-column: 1/2; } }

//FOOTER
.page-footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: $color-primary;
  background-image: url("../../assets/footer-bg.png");
  background-repeat: repeat;
  background-size: 25%;
  padding-bottom: 4rem;
  .contact-footer, .navigation-footer {
    @include layout-gutter-y;
    @include layout-gutter-x; } }
